.layout-footer {
    transition: margin-left $transitionDuration;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 0;
    border-top: 1px solid var(--surface-border);

    @media screen and (max-width: 576px) {
        a {
          display: none;  
        }
    }
}
