.disclosure-page {

  .hero-image {
    max-width: 100%;
    object-fit: cover;
  }

  .description-area {
    border: 1px solid var(--indigo-100);
    border-radius: 8px;
    padding: 24px;
    background-color: white;
    margin: 0 20px;
    font-size: 1.25rem;

    li {
      list-style-type: none;
      line-height: 1.8;
  
      a {
        text-decoration: underline;
      }
    }

    @media screen and (max-width: 576px) {
      margin: 0;
      padding: 12px;
    }
  }

  .section {
    min-width: 360px;
    padding: 10px 0;
  }

  .border-left {
    border-left: 1px solid #ccc;

    @media screen and (max-width: 992px) {
      border-left: none;
    }
  
  }

  .tools {
    li:before {
      content: '⚙️';
      margin-left: -20px;
      margin-right: 10px;
    }
  }

  .workflow {
    li:before {
      content: '🔻';
      margin-left: -20px;
      margin-right: 10px;
    }
  }

  .game-shot {
    width: 50%;
    height: 264px;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;

    @media screen and (max-width: 576px) {
      width: 100%;
    }

    img {
      max-width: 100%;
      object-fit: cover;
    }

    .interactive {
      cursor: pointer;
      transition: transform .2s; /* Animation */
      &:hover {
        opacity: 1;
        transform: scale(1.02);
        box-shadow: 0 4px 4px -1px rgba(0,0,0,.2), 0 1px 1px 0 rgba(0,0,0,.14), 0 1px 3px 0 rgba(0,0,0,.12);
      }
    }

    .thumbnail {
      width: 50%;
      height: 25%;
      padding: 4px 8px 4px 0px;
      opacity: 0.6;
    }

    .titlebar {
      bottom: -54px;
      left: 10px;
      z-index: 10;
      background-color: rgba(0,0,0,.6);
      color: white;
      padding: 10px;
      width: calc(100% - 20px);
      
      &.show {
        transition: .5s ease-in-out;
        transform: translateY(-50px);
      }
    }

  } // end of game-shot

  .tab {
    color: #666;
    border-bottom: 2px solid #ccc;
    margin-top: 0.25rem;
    margin-bottom: 0;
    padding-bottom: 1rem;
    flex: 1;
    text-align: center;
    user-select: none;
    cursor: pointer;

    &.selected {
      color: var(--blue-800);
      border-bottom: 2px solid var(--primary-color);
    }

    @media screen and (max-width: 576px) {
      font-size: 18px;
    }
  }

  .stay-tuned {
    background-color: var(--indigo-600);
    color: white;
    border-radius: 8px;
    width: 100%;
    height: 100%;
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .game-doc-code {
    min-height: 520px;
    overflow: scroll;
    font-size: 1.25rem;

    a {
      &:hover {
        text-decoration: underline;
      }
    }

    blockquote {
      margin-top: 2.5rem;
    }

    h2 {
      padding: 4px;
      background-color: bisque;
      border-radius: 4px;
    }

    h2{
      margin-bottom: 2rem;
    }

    h3 {
      margin: 2rem 0;
    }

    h4 {
      padding: 4px;
      background-color:cornsilk;
      border-radius: 4px;
    }

    ul {
      padding-left: 30px;

      li {
        line-height: 1.5;
      }

      a {
        text-transform: uppercase;
      }
    }

    code {
      background-color: #f5f5f5;
      font-size: 1.15rem;
      text-shadow: 1px 1px 2px pink;
    }

    img {
      max-width: 640px;
      height: auto;
    }

    img.reactive {
      cursor: pointer;
      &:hover {
        border: 2px solid orange;
      }
    }
  }

  @media (max-width: 768px) {
    padding-top: 4rem;
  }

}